* {
  box-sizing: border-box;
}

.carousel-container {
  max-width: 1000px;
  position: relative;
  margin-top: 20px;
}

.carousel-image {
  width: 100%;
  max-height: 560px;
}

.carousel-buttons {
  text-align: center;
}

.carousel-buttons button {
  margin: 5px;
}

.app-wrap {
  width: 90%;
  max-width: 1280px;
  margin: 8px auto;
  color: #000;
  background-color: #f5f5f5;
  font-size: 13.3333px;
  font-family: Verdana, Geneva, sans-serif;
}

.app-header {
  padding: 6px;
  line-height: 18px;
  vertical-align: middle;
  text-align: middle;
  position: relative;
}

.app-header h1 {
  text-align: center;
}

.app-content {
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.app-footer {
  margin-top: 1em;
  border-top: 1px solid #e7e7e7;
  text-align: center;
  color: #333;
  padding: 6px 0;
}

.app-footer a {
  color: inherit;
  text-decoration: underline;
}

.location-form input[type='text'] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
  margin-right: 10px;
}

/* Style the submit button */
.location-form button {
  float: left;
  width: 18%;
  padding: 10px;
  background: #2196f3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
}

/* Clear floats */
.location-form::after {
  content: '';
  clear: both;
  display: table;
}
